@import"https://fonts.googleapis.com/css2?family=Bebas+Neue:wght@400&family=Space+Grotesk:wght@400;600;700&display=swap";:root{--display-font: "Bebas Neue", "TerminaTest", Impact, system-ui, sans-serif;--body-font: "Space Grotesk", system-ui, sans-serif}.gigs-section{color:#fff;text-align:center;width:100%;font-family:var(--body-font)}.gigs-title{color:#07611a;font-size:3rem;font-weight:100;font-family:"TerminaTest",var(--display-font),sans-serif;margin-bottom:10px;text-align:left;letter-spacing:1px}@media(min-width: 768px){.gigs-title{font-size:5rem}}.gigs-table{display:flex;flex-direction:column;gap:.5rem}.gig-row{display:grid;grid-template-columns:2fr 3fr 2fr;border:2px solid #fff;background-color:#000;transition:background-color .2s,color .2s,transform .15s ease;cursor:pointer;font-size:2rem}@media(min-width: 1000px){.gig-row{font-size:3rem}}.gig-row:hover{background-color:#00f114;transform:translateY(-1px)}.gig-row:hover .gig-event,.gig-row:hover .gig-date,.gig-row:hover .gig-location{background-color:#00f114;color:#000}.gig-date,.gig-location{background-color:#07611a;padding:1rem;display:flex;justify-content:center;align-items:center;font-weight:bold}.gig-event{background-color:#000;padding:.75rem 1rem;display:grid;align-content:center;gap:.4rem;text-align:left}.gig-event-title{font-weight:800;line-height:1.1;font-size:clamp(1rem,1.6vw,2.8rem);text-transform:uppercase;letter-spacing:.5px;text-align:center}.gig-event-extras{display:flex;flex-wrap:wrap;gap:.4rem .6rem;align-items:center}.gig-badge{font-size:.55em;font-weight:800;background:#0ea83a;color:#031b08;padding:.15em .5em;border-radius:999px;line-height:1.2}.gig-address-inline,.gig-description-inline{font-size:.5em;color:#d8d8d8;line-height:1.2}.gig-description-inline{display:inline-block;max-width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.gig-link-inline{font-size:.55em;font-weight:700;color:#9be5b0;text-decoration:underline}.toggle-past{margin-top:1rem;width:100%;display:flex;justify-content:space-between;align-items:center;background:rgba(0,0,0,0);color:#eaeaea;border:1px solid hsla(0,0%,100%,.18);border-radius:14px;padding:.9rem 1rem;cursor:pointer;text-align:left;transition:border-color .2s ease,background .2s ease,transform .1s ease;font-size:1.1rem}.toggle-past:hover{border-color:#0ea83a;background:rgba(14,168,58,.06)}.toggle-past.open{background:rgba(14,168,58,.08)}.chevron{width:10px;height:10px;border-right:2px solid currentColor;border-bottom:2px solid currentColor;transform:rotate(45deg);transition:transform .2s ease}.toggle-past.open .chevron{transform:rotate(-135deg)}.past-outer{overflow:hidden;transition:height 300ms ease,opacity 250ms ease;will-change:height,opacity}@media(prefers-reduced-motion: reduce){.past-outer{transition:none}}.gig-modal{position:fixed;inset:0;background:rgba(0,0,0,.92);display:grid;place-items:center;z-index:999;padding:1rem}.gig-modal-content{position:relative;background:#101;border:1px solid hsla(0,0%,100%,.12);border-radius:18px;width:min(1100px,92vw);max-height:90vh;overflow:hidden;display:grid;grid-template-columns:1fr;gap:0}@media(min-width: 900px){.gig-modal-content{grid-template-columns:1.4fr 1fr}}.modal-close{position:absolute;top:10px;right:12px;background:rgba(0,0,0,.6);border:1px solid hsla(0,0%,100%,.2);color:#fff;width:36px;height:36px;border-radius:50%;font-size:1.2rem;line-height:1;cursor:pointer;z-index:2}.modal-close:hover{background:rgba(0,0,0,.8)}.modal-media{background:#000}.modal-media img{width:100%;height:100%;max-height:90vh;object-fit:cover;display:block}.modal-body{padding:1.25rem;overflow-y:auto;display:grid;gap:.9rem;text-align:left;max-width:720px;margin-inline:auto;font-family:var(--body-font)}.modal-body a{font-size:32px;margin:10px 10px;color:#fff;transition:transform .3s ease}.modal-title{margin:0;font-family:var(--display-font);letter-spacing:.5px;font-weight:400;line-height:1;font-size:clamp(6rem,2.6vw,3rem);color:#e9ffe6;text-align:center}.modal-meta{list-style:none;margin:.25rem 0 0;padding:0;display:flex;flex-wrap:wrap;gap:.6rem .7rem}.modal-meta .meta-item{height:35px;display:inline-flex;align-items:baseline;gap:.45rem;padding:.5rem .75rem;border-radius:999px;background:rgba(14,168,58,.1);border:1px solid rgba(155,229,176,.25);white-space:nowrap;font-size:clamp(2rem,1.1vw,1.2rem);line-height:1.1;color:#e5e5e5}.modal-meta .meta-item strong{color:#9be5b0;font-weight:700}.meta-date::before{content:"üìÖ";margin-right:.3rem}.meta-time::before{content:"‚è∞";margin-right:.3rem}.meta-location::before{content:"üìç";margin-right:.3rem}.meta-address::before{content:"üß≠";margin-right:.3rem}.modal-description{color:#d8d8d8;font-size:clamp(1rem,1.05vw,1.2rem)}.modal-description a{color:#9be5b0;text-decoration:underline}.gig-link{margin-top:.5rem;align-self:start;display:inline-flex;gap:.5rem;align-items:center;padding:.6rem .9rem;border-radius:999px;font-weight:700;background:#07611a;color:#fff;text-decoration:none;transition:filter .2s ease,transform .1s ease}.gig-link:hover{filter:brightness(1.1)}.gig-link:active{transform:translateY(1px)}.gig-link.large{padding:.7rem 1.1rem;font-size:1rem}